showPortfolio();
function showPortfolio() {
  const portfolioBlocks = document.querySelectorAll('.block-portfolio--hidden');

  for(index = 0; index < portfolioBlocks.length; index++) {

    let portfolioBlock = portfolioBlocks[index];
    const btnAdd = document.querySelector('.portfolio__btn_add');

    if(window.innerWidth <= 768){
      portfolioBlock.classList.add('display-none');
      btnAdd.classList.remove('display-none');
    }

    btnAdd.addEventListener('click', () => {
      portfolioBlock.classList.remove('display-none');
      btnAdd.remove();
    })    
  }
  
  
}


showPopup();
function showPopup() {
  const popupLinks = document.querySelectorAll('.popup-link');
  const body = document.querySelector('body');
  const lockPadding = document.querySelectorAll('.lock-padding');
  let unlock = true;
  const timeout = 600;

if (popupLinks.length > 0) {
  for( let item = 0; item < popupLinks.length; item++) {
    const popupLink = popupLinks[item];
    popupLink.addEventListener('click', (e) => {
      const popupName = popupLink.getAttribute('href').replace('#', '');
      const curentPopup = document.getElementById(popupName);
      popupOpen(curentPopup);
      e.preventDefault();
    });
  }
}
const popupCloseIcon = document.querySelectorAll('.close-popup');
if (popupCloseIcon.length > 0) {
  for (let item = 0; item < popupCloseIcon.length; item++) {
    const el = popupCloseIcon[item];
    el.addEventListener('click', (e) => {
      popupClose(el.closest('.popup'));
      e.preventDefault();
    })
  }
}

function popupOpen(curentPopup) {
  if (curentPopup && unlock) {
    const popupActive = document.querySelector('.popup.open');
    if (popupActive) {
      popupClose(popupActive, false);      
    } else{
      bodylock();
    }
    curentPopup.classList.add('open');
    curentPopup.addEventListener('click', function (e) {
      if (!e.target.closest('.popup__content')) {
        popupClose(e.target.closest('.popup'));
      }
    });
  }
}
function popupClose(popupActive, doUnlock = true) {
  if (unlock){
    popupActive.classList.remove('open');
    if(doUnlock) {
      bodyUnlock();
    }
  }
}
function bodylock () {
  const lockPaddingValue = window.innerWidth - document.querySelector('.wrapper').offsetWidth + 'px';

  if(lockPadding.length > 0) {  
    for (let item = 0; item<lockPadding.length; item++) {          
      const el = lockPadding[item];
      el.getElementsByClassName.paddingRight = lockPaddingValue;    
    }
  }
  body.style.paddingRight = lockPaddingValue;
  body.classList.add('lock');

  unlock = false;
  setTimeout( () => {
    unlock = true;
  }, timeout)
}

function bodyUnlock() {
  setTimeout( () => {
    if (lockPadding.length > 0) {
      for (let item = 0; item < lockPadding.length; item++) {
        const el = lockPadding[item];
        el.style.paddingRight = '0px';
      }
    }
    body.style.paddingRight = '0px';
    body.classList.remove('lock');    
  }, timeout);

  unlock = false;
  setTimeout( () => {
    unlock = true;
  }, timeout)
}

document.addEventListener('keydown', (e) => {
  if (e.which === 27) {
    const popupActive = document.querySelector('.popup.open');
    popupClose(popupActive);
  }
});
(function () {
  // проверяем поддержку
  if (!Element.prototype.closest) {
    // реализуем
    Element.prototype.closest = function (css) {
      var node = this;
       while (node) {
         if (node.matches(css)) return node;
         else node = node.parentElement;
       }
       return null;
    };
  }
})();
(function () {
  // проверяем поддержку
  if(!Element.prototype.matches) {
    // определяем свойство
    Element.prototype.matches = Element.prototype.matchesSelector ||
      Element.prototype.webkitMatchesSelector ||
      Element.prototype.mozMatchesSelector ||
      Element.prototype.msMatchesSelector;
  }
})();
  
}